import React from 'react'
import data from '../data1/people.json'
import FindList from '../components/FindList'
import '../styles/People.scss'
//
export default  class FindMain extends React.Component {

  render()
  {
        let listArr=[];
    data.forEach((item,index)=>{
        listArr.push(<FindList key={index} title={item.title} more={item.more} people={item.people}/>)
    });

    return (
      <div className="main app">
        <div className="search"><div className="box"><span className="glyphicon glyphicon-search"></span></div></div>
        <div>
          <ul className="item-nav">
            <li   style={{backgroundImage:`url(${require('../images/bg1.png')})`,marginLeft:0,cursor:"pointer"}}><span style={{display:"block",paddingTop:"3.15rem"}}></span></li>
            <li  style={{backgroundImage:`url(${require('../images/bg2.png')})`,cursor:"pointer"}}><span style={{display:"block",paddingTop:"3.15rem"}}></span></li>
            <li  style={{backgroundImage:`url(${require('../images/bg3.png')})`,marginRight:0,cursor:"pointer"}}><span style={{display:"block",paddingTop:"3.15rem"}}></span></li>
          </ul>
        </div>
        <div>
          <ul className="sub-nav">
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
          </ul>
        </div>

        {listArr}

      </div>
    )
  }
}





